// Create or update profile
export const createProfile = (
  formData,
  history,
  edit = false
) => async dispatch => {
  try {
    const config = {
      headers: {
        'Content-Type': 'application/json'
      }
    };

    const res = await axios.post('/api/profile', formData, config);
    
    dispatch({
      type: GET_PROFILE,
      payload: res.data
    });
     console.log('createprofile post api profile',res,'formData',formData)
    dispatch(setAlert(edit ? 'Profile Updated' : 'Profile Created', 'success'));

    if (!edit) {
        console.log('createprofile!edit',!edit)
      history.push('/dashboard');
    }
  } catch (err) {
      console.log('createprofile err',err)
    const errors = err.response.data.errors;

    if (errors) {
        console.log('createprofile if errors',errors)
      errors.forEach(error => dispatch(setAlert(error.msg, 'danger')));
    }

    dispatch({
      type: PROFILE_ERROR,
      payload: { msg: err.response.statusText, status: err.response.status }
    });
  }
};

// Add Experience
export const addExperience = (formData, history) => async dispatch => {
  try {
    const config = {
      headers: {
        'Content-Type': 'application/json'
      }
    };

    const res = await axios.put('/api/profile/experience', formData, config);
     console.log('addexperience api/profile/experience',res,'formdata',formData)
    dispatch({
      type: UPDATE_PROFILE,
      payload: res.data
    });

    dispatch(setAlert('Experience Added', 'success'));

    history.push('/dashboard');
  } catch (err) {
      console.log('add experience err',err)
    const errors = err.response.data.errors;

    if (errors) {
        console.log('add experience if errors',errors)
      errors.forEach(error => dispatch(setAlert(error.msg, 'danger')));
    }

    dispatch({
      type: PROFILE_ERROR,
      payload: { msg: err.response.statusText, status: err.response.status }
    });
  }
};

